@import url(https://fonts.googleapis.com/css2?family=Cinzel:wght@800&family=Lora&family=Open+Sans:wght@300&family=Overpass&display=swap);
.centered {
  text-align: center;
}

h2 {
  font-size: 1rem;
}

.App {
  font-family: "Open Sans", sans-serif;
  padding: 1.5rem;
  --border-radius: 5px;
}

.btn {
  display: block;
  cursor: pointer;
  position: relative;
  width: auto;
  color: black;
  border: none;
  border-bottom: 1px white solid;
  border-radius: 3px;
  padding: 0.5rem 0.5rem;
  background-color: rgba(0, 0, 0, 0);
  transition: all 0.2s;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.btn::after {
  content: "";
  width: 0%;
  height: 100%;
  position: absolute;
  left: 50%;
  top: 0;
  border-bottom: 1px black solid;
  transition: all 0.5s;
}

.btn:hover {
  font-size: 1.2rem;
  padding: 0.3rem 0.2rem;
}

.btn:hover::after {
  left: 0;
  width: 100%;
}

.btn:active {
  margin-top: 5px;
  margin-bottom: -5px;
}

.btn:disabled {
  cursor: auto;
  font-size: 1rem;
  padding: 0.5rem 0.5rem;
  border-color: white;
  color: grey;
  background-color: transparent;
}

.btn:disabled::after {
  display: none;
}

.App-main {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
}

.App-main > div {
  min-width: 40vw;
  max-width: 500px;
  margin: 0 2rem;
}

@media (max-width: 877px) {
  .App-main {
    flex-direction: column;
    align-items: center;
  }
  .App-main > div {
    min-width: 80vw;
    margin: 0;
  }
}

.collapsable {
  position: relative;
}

.collapsable-collapsed {
  margin: 0 auto;
  margin-bottom: 1rem;
}

.collapsable-collapsed > .btn-collapsable {
  position: relative;
  width: 100%;
  border: 1px dashed black;
  min-width: 150px;
  padding: 0.5rem;
}

.btn-collapsable {
  position: absolute;
  cursor: pointer;
  right: 0;
  top: 0;
  border: 1px solid black;
  background: none;
  transition: all 0.2s;
}

.btn-collapsable:hover {
  border: 1px dotted grey;
}

@media (max-width: 877px) {
  .collapsable-collapsed {
    width: 80vw;
  }

  #fixed-bottom {
    position: fixed;
    left: 0;
    bottom: 0;
    z-index: 100;

    width: 100%;
    margin: 0;

    text-align: center;
    background: white;
    border-top: 1px black solid;
  }

  #fixed-bottom .btn-collapsable {
    margin-top: 1rem;
  }

  #fixed-bottom.collapsable-collapsed > .btn-collapsable {
    margin-top: 5px;
  }
}

.console {
  border: 1px solid black;
  border-radius: var(--border-radius);
  height: 10em;
  margin-bottom: 1rem;
  overflow-x: hidden;
}

.console-text {
  overflow-y: scroll;
  margin-right: -16px;
  padding-right: 16px;
  height: 100%;
}

.console-text > pre {
  padding-left: 5px;
  font-family: "Open Sans", sans-serif;
}

.with-input {
  --console-padding: 2;
  position: relative;
  padding-bottom: 2rem;
}

.with-input > .console-input {
  position: absolute;
  bottom: 0;
  height: 2rem;
  width: 100%;
  padding: var(--console-padding);
  box-sizing: content-box;
  border: none;
}

.console-input > input {
  font-family: "Open Sans", sans-serif;
  position: relative;
  border: none;
  border-top: 1px solid black;
  outline: none;
  width: calc(100% - 4px);
  height: calc(100% - 4px);
}

@media (max-height: 877px) {
  .console {
    height: 6rem;
  }
}

.panel {
  padding: 0;
  list-style-type: none;
  width: 100%;
  display: flex;
  justify-content: space-evenly;
}

.note {
  margin: 0;
  color: rgb(100, 100, 100);
}

.editor-container {
  height: 320px;
  overflow: auto;
  border: 1px solid black;
  border-radius: var(--border-radius);
}

.manual-table th,
td {
  text-align: left;
  padding-left: 10px;
}

.bordered-text {
  font-size: 0.8rem;
  padding: 0.1rem;
  border: 1px solid black;
}

.program-field {
  margin-top: 1rem;
  padding: 1rem;
  border: 1px black solid;
  border-radius: var(--border-radius);
  height: 12rem;
  position: relative;
  overflow-y: auto;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  align-content: flex-start;
}

.program-field > span {
  color: black;
  font-size: 1rem;
  transition: 0.04s all;
  cursor: pointer;
  border: 1px solid transparent;
  position: relative;
}

.program-field > span:hover {
  border: 1px solid red;
}

.program-field > .highlighted {
  color: red;
}

.program-field > .highlighted::before {
  content: "^";
  position: absolute;
  bottom: -60%;
}

.program-field > .breakpoint {
  border: 1px solid red;
}

.program-grid {
  display: grid;
  grid-template-columns: 3fr 1fr;
  grid-template-rows: 1fr;
  grid-column-gap: 25px;
  grid-row-gap: 0px;
}

.program-grid:nth-child(1) {
  grid-area: 1 / 1 / 2 / 2;
}

.program-grid:nth-child(2) {
  grid-area: 1 / 2 / 2 / 3;
}

@media (max-width: 877px) {
  .program-field {
    height: 6rem;
  }
}

@media (max-width: 1043px) {
  .visualization {
    display: block;
  }
}

.memory-field {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  margin-top: 1rem;
}

.memory-block {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 0.3fr 1fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  border: 1px solid black;

  min-width: 96px;
  margin: 10px 10px;
  text-align: center;
}

.memory-block > div:nth-child(1) {
  grid-area: 1 / 1 / 2 / 2;
  border-bottom: black 1px solid;
}

.memory-block > div:nth-child(2) {
  grid-area: 2 / 1 / 3 / 2;
  margin: 0 4px;
  font-size: 0.85rem;
}

.memory-block-highlighted {
  border-color: red;
}

.memory-block-highlighted > div:nth-child(1) {
  border-bottom: 1px red solid;
}

body {
  font-family: monospace;
  margin: 0;
  overflow-x: hidden;
}

